@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@500&family=JetBrains+Mono:wght@500&display=swap);
body {
	margin: 0;
	height: 100%;
	background-color: whitesmoke;
}

html {
	height: 100%;
}

#root {
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;

	--main-font: "JetBrains Mono", monospace;
	--alternative-font: "IBM Plex Mono", monospace;
	font-family: var(--main-font);
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

.image-container img {
	max-height: 100%;
	width: auto;
}

/* Remove arrows from number inputs */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}
input[type="number"] {
	-moz-appearance: textfield;
}

.alarm-box-container {
	width: 100px;
	height: 340px;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	margin-left: 4px;
}

.add-alarm-container {
	width: 100%;
	height: 20px;
	display: flex;
}

.add-alarm-container * {
	font-family: var(--main-font);
	font-size: 10px;
}

.add-alarm-input {
	font-size: 23px;
	border: none;
	background: none;
	border-bottom: 2px solid rgba(250, 243, 235, 1);
	color: rgba(250, 243, 235, 1);
	width: 76px;
	cursor: text;
}

.add-alarm-input:focus {
	outline: none;
}

.add-alarm-button {
	width: 20px;
	background: none;
	border: 1.4px rgba(250, 243, 235, 1) solid;
	border-radius: 50%;
	margin-left: 4px;
	padding: 3px;
}

.add-alarm-button:hover {
	background: rgba(250, 243, 235, 0.2);
	cursor: pointer;
}

.add-alarm-button:active {
	background: rgba(250, 243, 235, 0.6);
	color: black;
}

.alarm-box {
	position: relative;
	display: flex;

	margin: 1.8px;
	width: 96%;
	height: 30.8px;
	padding: 1px 1px 0 1px;
	font-size: 12px;
	border-radius: 2%;
	color: rgba(250, 243, 235, 1);

	border: 1.2px solid rgba(250, 243, 235, 1);
	background: rgba(250, 243, 235, 0.2);

	align-items: center;
	justify-content: space-between;
}

.alarm-box-text-box {
	display: flex;
	align-items: baseline;
}

.alarm-box-text-box > span:nth-of-type(1) {
	align-self: flex-start;
}

.alarm-box-text-box > span:nth-of-type(2) {
	font-size: 20px;
}

.alarm-box-text-box > span:nth-of-type(3) {
	font-size: 8px;
	padding-left: 1px;
}

.alarm-box.alert {
	border: none;
	background-color: tomato;
	box-shadow: 0 0 1px 2px greenyellow inset;
}

.alarm-box:hover .small-button {
	visibility: visible;
}

.small-button-box {
	position: absolute;
	top: 0;
	right: 0;
	display: flex;
	width: -webkit-max-content;
	width: max-content;
	height: -webkit-max-content;
	height: max-content;
	margin-right: 2px;
}

.small-button {
	visibility: hidden;

	background: none;
	border: none;
	padding: none;
	margin: 2px 2px 0px 0px;
	width: 10px;
	height: 10px;
	opacity: 0.9;
	cursor: pointer;

	justify-self: flex-end;
	align-self: flex-start;
}

.small-button:hover {
	opacity: 0.75;
}

.small-button:active {
	opacity: 1;
}

.alarm-box:nth-of-type(n + 11) {
	-webkit-transform: translateY(20px);
	        transform: translateY(20px);
}

.alarm-box:nth-of-type(n + 65) {
	display: none;
}

.graph-container {
	position: relative;
	position: absolute;

	bottom: 0px;
	left: 0px;

	width: 100%;
	height: 100%;

	display: flex;

	/* border: 3px rgba(250, 243, 235, 1) solid; */
	padding-right: 10px;
	overflow: visible;

	color: rgba(250, 243, 235, 1);
	justify-content: flex-end;

	z-index: 1;
}

.line-graph-container {
	z-index: 300;
	background: none;
	width: 100%;
	height: 100%;
	position: absolute;
	overflow: visible;
}

.line-graph-point {
	-webkit-transform: rotate(-45deg);
	        transform: rotate(-45deg);
	width: 4px;
	height: 4px;
	background-color: tomato;
	border: red  1px solid;
	box-shadow: 0 0 0 1px black;
	position: absolute;

	z-index: 400;
}

.graph-bar {
	box-shadow: 0 0 0 .1px black;
}

.graph-info-box {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	justify-self: flex-end;

	opacity: 0.9;
}

.graph-info-box > div:nth-of-type(n + 2) {
	margin-top: -13px;
	background: rgba(250, 243, 235, 0.4);
	border-radius: 50%;
	width: 40px;
	height: 40px;
	display: flex;
	align-items: center;
	justify-content: center;

    text-shadow: 1px 1px 0 black;
}

.graph-info-box > div:nth-of-type(2) {
	background-color: rgba(247, 26, 0, 0.7);
	z-index: 14;
	margin-top: 5px;
}
.graph-info-box > div:nth-of-type(3) {
	background-color: rgba(255, 237, 48, 0.7);
	z-index: 13;
}
.graph-info-box > div:nth-of-type(4) {
	background-color: rgba(0, 135, 57, 0.7);
	z-index: 12;
}
.graph-info-box > div:nth-of-type(5) {
	background-color: rgba(102, 51, 153, 0.7);
	z-index: 11;
}

.app {
	width: 100vw;
	height: 100vh;
	background: linear-gradient(
		90deg,
		rgba(2, 0, 36, 1) 0%,
		rgba(124, 60, 60, 1) 35%,
		rgba(0, 113, 136, 1) 100%
	);
	display: flex;
	justify-content: center;
	align-items: center;
}

.gas-widget {
	opacity: .9;
	z-index: 200;
	display: flex;
}

.gas-frame-container {
	position: relative;
	width: 440px;
	height: 330px;
	box-shadow: 0 0 2px 1px rgb(238, 238, 238);
	background-color: hsl(0, 0%, 99.2%);
	border-radius: 2%;
	padding: 0.3rem 0.75rem 0.75rem 0.75rem;
}

.gas-frame {
	display: grid;
	height: 100%;
	width: 100%;
	grid-template-columns: repeat(2, 204px);
	grid-template-rows: 120px 120px auto;
}

.price-box {
	display: flex;
	width: calc(100% - 6px);
	flex-direction: column;
	background-color: rgba(250, 243, 235, 0.3);
	margin: 3px;
	padding: 3px 15px;
	justify-content: center;
}

.price {
	font-size: 48px;
	align-self: center;
}

.standard {
	box-shadow: inset 0px 0px 0px 3px rgba(250, 243, 235, 1);
}

.tx-cost {
	align-self: center;
	opacity: 0.6;
}

.timestamp {
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	display: flex;
	flex-direction: column;
	grid-column: 1 / span 1;
	font-size: 11px;
}
.kill-button-container {
	width: 100%;
	height: 100%;
	position: relative;
	grid-column: 2 / span 1;
	grid-row: 3 / span 1;

}

.kill-button {
	justify-self: center;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	width: calc(100% - 6px);
	height: 32px;
	background: none;
	padding: 0.5% 1%;
	border: 3px solid rgba(250, 243, 235, 1);
	font-family: var(--main-font);
	font-size: 18px;
	margin: 0 0 0 2px;
}

.kill-button:hover {
	cursor: pointer;
	background-color: rgba(250, 243, 235, 0.3);
}

.kill-button:active {
	background-color: rgba(250, 243, 235, 0.8);
}

.autoconnect-checkbox {
	position: absolute;
	right: 16px;
	bottom: 20px;
	font-size: 10px;

}

.eth-price-container {
	grid-column: 1 / span 1;
	height: 40px;
	font-size: 16px;
	display: flex;
	align-items: flex-start;
	margin-top: 5px;
}

.tx-select {
	grid-row: 4 / span 1;
	grid-column: 2 / span 1;

	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: baseline;
	height: 40px;
	padding: 6px 6px 0 2px
}

.tx-select * {
	font-size: 14px;
	margin: 0 0 5px 0;
}

#gasCostInput {
	font-family: var(--main-font);
	border: none;
	box-shadow: 0 0 1px 2px rgba(250, 243, 235, 1);
	width: 120px;
}

