@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@500&family=JetBrains+Mono:wght@500&display=swap");

body {
	margin: 0;
	height: 100%;
	background-color: whitesmoke;
}

html {
	height: 100%;
}

#root {
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;

	--main-font: "JetBrains Mono", monospace;
	--alternative-font: "IBM Plex Mono", monospace;
	font-family: var(--main-font);
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

.image-container img {
	max-height: 100%;
	width: auto;
}

/* Remove arrows from number inputs */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}
input[type="number"] {
	-moz-appearance: textfield;
}
